@import './font/WebComponentsIcons';

@include exports( "common/icons" ) {
    $icon-font-url: map-get( $data-uris, 'WebComponentsIcons.ttf' ) !default;

    @font-face {
        font-family: 'WebComponentsIcons';
        font-style: normal;
        font-weight: normal;
        src: url(#{$icon-font-url}) format( "truetype" );
    }

    .k-icon {
        width: 1em;
        height: 1em;
        -moz-osx-font-smoothing: grayscale;
        -webkit-font-smoothing: antialiased;
        font-size: $icon-size;
        font-family: 'WebComponentsIcons';
        font-style: normal;
        font-variant: normal;
        font-weight: normal;
        line-height: 1;
        speak: none;
        text-transform: none;
        text-decoration: none;
        display: inline-block;
        vertical-align: middle;

        &::before {
            vertical-align: baseline;
        }

        &:hover,
        &:focus {
            text-decoration: none;
        }
    }

    .k-icon-with-modifier {
        position: relative;
        margin: .25em;
    }

    .k-icon-modifier {
        position: absolute;
        font-size: .5em;
        bottom: 0;
        right: 0;
        margin: 0 -.5em -.5em 0;
    }

    .k-i-none::before {
        content: "";
        display: none;
    }

    .k-icon-xs { font-size: ( $icon-size * .5 ); }
    .k-icon-sm { font-size: ( $icon-size * .75 ); }
    .k-icon-md { font-size: ( $icon-size * 2 ); }
    .k-icon-lg { font-size: ( $icon-size * 3 ); }
    .k-icon-xl { font-size: ( $icon-size * 4 ); }

